export const fetchCitiesStat = async () => {
    const cities = [
        { name: 'Delhi NCR' },
        { name: 'Agra' },
        { name: 'Lucknow' },
        { name: 'Jaipur' },
        { name: 'Kolkata' },
        { name: 'Ranchi' },
        { name: 'Odisha' },
        { name: 'Varanasi' },
        { name: 'Pune' },
        { name: 'Kanpur' },
        { name: 'Consulting' }
    ]



    let response = {
        'Delhi NCR':0,
        'Agra': 0,
        'Lucknow': 0,
        'Jaipur': 0,
        'Kolkata': 0,
        'Ranchi': 0,
        'Odisha': 0,
        'Varanasi': 0,
        'Pune': 0,
        'Kanpur': 0,
        'Consulting': 0
    } ;
    cities.forEach(async city => {
        let donations = 0
        try {
            let { totalDonation } = await fetchPostCamp(city.name)
            donations = totalDonation
        } catch (e) {
            console.log('Error at', city,e)
            donations = 0
        }
        response[city.name] = donations
    })

    return response
}
